import React from 'react';
import { Link } from 'react-router-dom';
import Button from '../components/Button';
import Helmet from '../components/Helmet';

const Success = () => {
    return (
        <Helmet title="Success">
            <div className="page_success">
                <h2>Bn  t hng thnh cng!</h2>
                <div className="page_success_txt">
                    <span>Chng ti s lin lc vi bn qua s in thoi  xc nhn li n hng. </span>
                    <span>Xin chn thnh cm n qu khch!</span>
                </div>
                <Link to="/for-him">
                    <Button size="sm">Tip tc mua hng</Button>
                </Link>
            </div>
        </Helmet>
    );
};

export default Success;
