import React from 'react';

import { Link } from 'react-router-dom';

import Grid from './Grid';

const footerAboutLinks = [
    {
        display: 'Gii thiu',
        path: '/about',
    },
    {
        display: 'Lin h',
        path: '/about',
    },
    {
        display: 'Tuyn dng',
        path: '/about',
    },
    {
        display: 'Tin tc',
        path: '/about',
    },
    {
        display: 'H thng ca hng',
        path: '/about',
    },
];
const footerCustomerLinks = [
    {
        display: 'Chnh sch i tr',
        path: '/about',
    },
    {
        display: 'Chnh sch bo hnh',
        path: '/about',
    },
    {
        display: 'Chnh sch hon tin',
        path: '/about',
    },
];

const Footer = () => {
    return (
        <footer className="footer">
            <div className="container">
                <Grid col={4} mdCol={2} smCol={1} gap={10}>
                    <div>
                        <div className="footer_title">Tng i h tr</div>
                        <div className="footer_content">
                            <p>
                                Lin h t hng <strong>0123456789</strong>
                            </p>
                            <p>
                                Thc mc n hng <strong>0123456789</strong>
                            </p>
                            <p>
                                Gp , khiu ni <strong>0123456789</strong>
                            </p>
                        </div>
                    </div>
                    <div>
                        <div className="footer_title">V chng ti</div>
                        <div className="footer_content">
                            {footerAboutLinks.map((item, index) => (
                                <p key={index}>
                                    <Link to={item.path}>{item.display}</Link>
                                </p>
                            ))}
                        </div>
                    </div>
                    <div>
                        <div className="footer_title">Chm sc khch hng</div>
                        <div className="footer_content">
                            {footerCustomerLinks.map((item, index) => (
                                <p key={index}>
                                    <Link to={item.path}>{item.display}</Link>
                                </p>
                            ))}
                        </div>
                    </div>
                    <div>
                        <div className="footer_title">SSSTUTTER</div>
                        <div className="footer_content">
                            <p>
                                Vi thng ip "Refined Life", SSStutter mong mun em n cho khch hng mt li sng
                                tinh gn bng cc sn phm thi trang tinh t.
                            </p>
                        </div>
                    </div>
                </Grid>
            </div>
        </footer>
    );
};

export default Footer;
