import React from 'react';

import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/react-splide/css';

const Promotion = () => {
    return (
        <div className="promotion_popup">
            <Splide
                options={{
                    type: 'loop',
                    perPage: 1,
                    rewind: true,
                    autoplay: true,
                    speed: 1000,
                    arrows: false,
                    pagination: false,
                }}
            >
                <SplideSlide>
                    <div className="promotion_popup_item">ng gi ship ch 10.000 cho n ni thnh</div>
                </SplideSlide>
                <SplideSlide>
                    <div className="promotion_popup_item">ng gi ship ch 20.000 cho n ton quc</div>
                </SplideSlide>
                <SplideSlide>
                    {' '}
                    <div className="promotion_popup_item">u i gim 10.000 khi thanh ton tr trc</div>
                </SplideSlide>
            </Splide>
        </div>
    );
};

export default Promotion;
